<template>
  <div>
    <div class="flex items-center justify-between">
        <div class="flex">
          <div class="absolute inset-x-0 top-[-41px] m-auto whitespace-nowrap">
            <span>{{currentDay | dateFmtROCMMDD}}</span>
          </div>
            <div>
              <font-awesome-icon icon="fa-solid fa-angle-left" @click="preNextDay(-1)" class="absolute right-[50px] top-[-41px] text-2xl  text-primary"/>
              <font-awesome-icon icon="fa-solid fa-angle-right" @click="preNextDay(1)" class="absolute right-[-3px] top-[-41px] text-2xl text-primary"/>
            </div>
        </div>
      </div>

      <div class="mt-2">
        <!-- head -->
        <div class="flex">
          <div class="w-[25%] border border-solid border-primary"></div>

          <div class="w-[75%] border border-solid border-primary">
            <span>{{$dayjs(currentDay).format('M/D') }}</span>
            <span>{{$dayjs(currentDay).format('(dd)') }}</span>
          </div>

        </div>
        <!-- <div class="flex">
          <div class="w-[25%] border border-solid border-primary">整日公休</div>
          <div class="flex w-[75%] border border-solid border-primary">
            <template v-for="(innerItem, innerIndex) in isRegularHoliday">
              <p
                v-if="innerItem.dates === $dayjs(currentDay).valueOf()"
                class="my-1 w-full overflow-hidden text-ellipsis whitespace-nowrap bg-second p-1 text-xs leading-tight text-white "
                :class="innerItem.customData.class"
                :key="`isRegularHoliday-${innerIndex}`"
              > {{innerItem.customData.title}}</p>
            </template>
          </div>

        </div> -->
        <!-- body -->
        <div>
          <div class="flex" v-for="(item,index) in attributes" :key="`resultItem${index}`">
            <div class="w-[25%] border border-solid border-primary">{{item.time}}</div>
            <div class=" flex w-[75%] cursor-pointer flex-wrap items-center justify-center border border-solid border-primary" v-for="(innerItem,innerIndex) in item.date" :key="`innerItem${innerIndex}`">
              <template v-if="Array.isArray(innerItem) && innerItem.length > 0">
                <template v-for="(order,index) in innerItem"  >
                <p
                  :key="`attributes${innerIndex}-${index}`"
                  class="my-1 w-full overflow-hidden text-ellipsis whitespace-nowrap bg-second p-1 text-xs leading-tight text-white "
                  :class="order.customData.class"
                  @click="doRoute(order)"
                  @keypress="doRoute(order)"
                >{{order.customData.title}}</p>
               </template>
              </template>
            </div>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  props: {
    value: {
      type: [String, Date],
      required: true,
    },
    weekLst: {
      type: [Array],
    },
    attributes: {
      type: [Array],
      default: () => ([]),
    },
    isRegularHoliday: {
      type: [Array],
      default: () => ([]),
    },
    manaulUpdateFormResult: {
      type: [Function],
      required: true,
    },
  },
  methods: {
    doRoute(attr) {
      console.log(attr);
      const unRouteList = ['break', 'regularHoliday'];
      if (unRouteList.includes(attr.customData.type)) {
        this.$swal.fire({
          icon: 'info',
          title: `${attr.customData.title}`,
          showConfirmButton: true,
          showCancelButton: true,
          confirmButtonText: '開放預約',
          customClass: {
            confirmButton: '!w-[144px] !rounded !bg-second !px-[14px] !py-2 !text-sm !text-white',
          },
          cancelButtonText: '關閉',
        }).then(async (res) => {
          if (res.isConfirmed) {
            if (attr.customData.type === 'break') {
              await this.$api.adminBookingTime({ designerId: this.userInfo.user.id, datetime: attr.dates, appointable: true });
            }
            if (attr.customData.class === 'bg-blue-400 text-white') {
              await this.$api.adminBookingTime({ designerId: this.userInfo.user.id, datetime: attr.dates, appointable: true });
            }
            if (attr.customData.class === 'bg-blue-600 text-white') {
              const bookingTimesPromiseArray = attr.customData.bookingTimes.map((item) => new Promise((resolve) => {
                resolve(this.$api.adminBookingTime({ designerId: this.userInfo.user.id, datetime: item.time, appointable: true }));
              }));
              await Promise.all(bookingTimesPromiseArray);
            }
            await this.manaulUpdateFormResult();
          }
        });
      } else {
        this.$router.push({ name: 'ScheduleBreak-Detail', query: { id: attr.key } });
      }
    },
    preNextDay(num) {
      this.currentDay = this.$dayjs(this.$dayjs(this.currentDay).add(num, 'days')).format('YYYY-MM-DD');
    },
  },
  computed: {
    ...mapState('userInfo', ['userInfo', 'currentMechantId']),
    currentDay: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit('input', value);
      },
    },
  },
};
</script>

