<template>
      <vc-date-picker
        locale="zh-TW"
        class="custom-calendar max-w-full"
        is-expanded
        :value="''"
        :attributes="attributesfilterdByCategories"
        :masks="masks"
        @update:to-page="swipeMonth($event)"
      >
        <template v-slot:header="{year,monthLabel}">
          <div class="absolute inset-x-0 top-[-45px] w-full">
            <div class="flex items-center justify-center">
              <span class="font-bold">{{year }}年</span>
              <span class="ml-2 text-2xl font-bold">{{monthLabel}}</span>
            </div>
          </div>
        </template>

        <template v-slot:header-left-button >
          <font-awesome-icon @click="preNextMonth(-1)" icon="fa-solid fa-angle-left" class="absolute right-[50px] top-[-41px] text-2xl text-primary"/>
        </template>

        <template v-slot:header-right-button>
          <font-awesome-icon @click="preNextMonth(1)" icon="fa-solid fa-angle-right" class="absolute right-[3px] top-[-41px] text-2xl text-primary"/>
        </template>

        <template v-slot:day-content="{ day, attributes }">
          <div class="z-10 flex h-full flex-col overflow-hidden">
            <span class="day-label text-sm text-gray-900">{{ day.day }}</span>
            <div class="grow overflow-auto">
              <template v-for="(attr,index) in attributes">
                <p
                  v-ripple="100"
                  v-if="toggleList[`day${day.day}`]? true : index < 3"
                  :key="attr.key + index"
                  class="mt-0 mb-1 max-w-[13vmin] break-words rounded-sm p-1 text-xs leading-tight"
                  @click="doRoute(attr)"
                  @keypress="doRoute(attr)"
                  :class="[attr.customData && attr.customData.class?attr.customData.class:'',{'pointer-events-none': !attr.customData}]"
                >
                  {{ attr.customData && attr.customData.title ? attr.customData.title :'' }}
                </p>
                <p v-if="toggleList[`day${day.day}`]? false:index === 4" v-ripple="100" :key="attr.key" @click="toggle(`day${day.day}`)" @keypress="toggle(`day${day.day}`)" class="mt-0 mb-1 rounded-sm bg-gray-400 p-1 text-xs leading-tight text-white">
                  +{{attributes.length - 3}}
                </p>
              </template>
            </div>
          </div>
        </template>
      </vc-date-picker>
</template>

<script>
import { mapState } from 'vuex';

export default {
  props: {
    value: {
      type: [Object, Array],
    },
    attributes: {
      type: [Array],
      default: () => ([]),
    },
    seletedCategories: {
      type: [Array],
      default: () => ([]),
    },
    manaulUpdateFormResult: {
      type: [Function],
      required: true,
    },
  },
  data() {
    return {
      masks: {
        weekdays: 'WWW',
      },
      toggleList: {},
    };
  },
  methods: {
    swipeMonth(e) {
      const y = e.year;
      const m = e.month - 1;
      const firstDay = new Date(y, m, 1);
      const lastDay = new Date(y, m + 1, 0);
      const starDate = this.$dayjs(firstDay).format('YYYY-MM-DD');
      const endDate = this.$dayjs(lastDay).format('YYYY-MM-DD');

      this.$emit('input', { start: starDate, end: endDate });
      this.toggleList = {};
    },
    doRoute(attr) {
      const unRouteList = ['break', 'regularHoliday', 'event'];
      if (unRouteList.includes(attr.customData.type)) {
        // 確認按鈕字樣，如果為"事件"，顯示刪除事件，預設為開放預約
        const confirmButtonText = attr.customData.class === 'bg-green-600 text-white' ? '刪除事件' : '開放預約';
        this.$swal.fire({
          icon: 'info',
          title: `${attr.customData.title}`,
          showConfirmButton: true,
          showCancelButton: true,
          confirmButtonText,
          customClass: {
            confirmButton: '!w-[144px] !rounded !bg-second !px-[14px] !py-2 !text-sm !text-white',
            cancelButton: '!py-2 !text-sm',
          },
          cancelButtonText: '關閉',
        }).then(async (res) => {
          if (res.isConfirmed) {
            if (attr.customData.type === 'break') {
              await this.$api.adminBookingTime({ designerId: this.userInfo.user.id, datetime: attr.dates[0].startTime, appointable: true });
            }
            if (attr.customData.class === 'bg-blue-400 text-white') {
              await this.$api.adminBookingTime({ designerId: this.userInfo.user.id, datetime: attr.dates[0].startTime, appointable: true });
            }
            if (attr.customData.class === 'bg-blue-600 text-white') {
              const bookingTimesPromiseArray = attr.customData.bookingTimes.map((item) => new Promise((resolve) => {
                resolve(this.$api.adminBookingTime({ designerId: this.userInfo.user.id, datetime: item.time, appointable: true }));
              }));
              await Promise.all(bookingTimesPromiseArray);
            }
            // 判斷資料為"事件"時，刪除
            if (attr.customData.class === 'bg-green-600 text-white') {
              await this.$api.deleteEvent({ eventId: attr.key });
            }
            await this.manaulUpdateFormResult();
          }
        });
      } else {
        this.$router.push({ name: 'ScheduleBreak-Detail', query: { id: attr.key } });
      }
    },
    preNextMonth(num) {
      this.$emit('input', { start: this.$dayjs(this.$dayjs(this.propMonthlyOrdersDate.start).add(num, 'month')).startOf('month').format('YYYY-MM-DD'), end: this.$dayjs(this.$dayjs(this.propMonthlyOrdersDate.end).add(num, 'month')).endOf('month').format('YYYY-MM-DD') });
      this.toggleList = {};
    },
    toggle(key) {
      this.toggleList[key] = true;
      this.$forceUpdate();
    },
  },
  computed: {
    ...mapState('userInfo', ['userInfo', 'currentMechantId']),
    isSeletedCategories() {
      return (type) => {
        let status = false;
        if (Array.isArray(this.seletedCategories)) {
          if (this.seletedCategories.length === 0) {
            status = false;
          } else {
            status = this.seletedCategories.includes(type);
          }
        }
        return status;
      };
    },
    attributesfilterdByCategories() {
      return this.attributes.filter((item) => this.isSeletedCategories(item.customData.type));
    },
    propMonthlyOrdersDate: {
      get() {
        return this.value;
      },
      set() {
        this.$emit('input', this.propMonthlyOrdersDate);
      },
    },
  },
};
</script>

